/* All elements with exceptions */
* {
  font-family: 'Montserrat', sans-serif;
  background-color: #FFF8FA;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0; /* Chrome */
}

input[type=number] {
    -moz-appearance:textfield; /* Firefox */
}

/* Title component */

.title {
  display: flex;
  margin: 3% 8%;
  flex-flow: row nowrap;
  justify-content : flex-start;
  border-bottom: #111111 solid 1px;
  padding: 4% 0;
}

.title img {
  height: 40px;
  margin: 0px 2%;
}

.title h1 {
  font-weight: 700;
  color: #EA4C89;
  margin: auto 0px;
  font-size: 1.7em;
}

/* Menu component */

.menu {
  display: flex;
  flex-flow: column nowrap;
}

.menu h2 {
  margin: 3% 8%;
  font-weight: 600;
  font-size: 1.5em;
}

.menu-item {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  border: #EA4C89 3px solid;
  margin: 2.5% 8%;
  border-radius: 12px;
  cursor: pointer;
}

.menu-item .borne-title {
  display: flex;
  flex-flow: row nowrap;
  background-color: transparent;
}

.menu-item img {
  height: 40px;
  margin: 5% 10%;
}

.menu-item li {
  list-style-type: none;
  font-weight: 500;
  margin: auto 0px;
  font-size: 1.4em;
  min-width: 120px;
}

/* Navigation element */

.nav {
  background-color: #FFC7DC;
  border: none;
  cursor: pointer;
  color: #111111;
  font-size: 1em;
  margin: auto 5%;
  border-radius: 100px;
  height: 35px;
  width: 35px;
}

/* Back component */

.back {
  display: flex;
  flex-flow: row nowrap;
  margin: 3% 8%;
  padding: 3% 0px;
}

.back h2 {
  font-weight: 600;
  font-size: 1.4em;
  margin: auto 1%;
}

/* Borne component */

.borne {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  margin: 0px auto;
  max-width: 500px;
}

.unvalidated {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
}

.wrongcode {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
}

.wrongcode img {
  height: 30px;
  width: 30px;
}

.wrongcode p {
  margin: auto 2%;
  font-size: 1.2em;
  font-weight: 300;
}

input.code {
  border: #EA4C89 3px solid;
  border-radius: 12px;
  background-color: #FFFFFF;
  font-weight: 600;
  font-size: 4em;
  text-align: center;
  outline: none;
  max-width: 50%;
  padding: 0px 5%;
  margin: 5% auto;
}

input.submit-button {
  background-color: #EA4C89;
  border: none;
  color: #FFFFFF;
  padding: 3% 5%;
  font-size: 1.2em;
  border-radius: 12px;
  max-width: 60%;
  margin: 5% auto;
  cursor: pointer;
}

.validated {
  margin: 0 auto;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
}

/* First borne */

.first.borne .validated p {
  font-weight: 600;
  font-size: 2.5em;
  margin: 6% auto;
}

.first.borne .validated img {
  height: 100px;
  width: 100px;
  margin: auto;
}

/* Second borne */

.second.borne {
  font-size: 1.4em;
  line-height: 50px;
  font-weight: 500;
  padding: 3% 8%;
}

/* Third borne */

.third.borne p.notre-dame {
  font-weight: 500;
  font-size: 1.4em;
  line-height: 150%;
  margin: 6% auto;
  text-align: center;
}

.third.borne p.date {
  font-weight: 600;
  font-size: 1.9em;
  margin: 6% auto;
  text-align: center;
}

.third.borne img {
  height: 75px;
  width: 75px;
  margin: auto;
}

/* Fourth borne */

.fourth.borne {
  font-size: 1.4em;
  line-height: 50px;
  font-weight: 500;
  padding: 3% 8%;
}

/* Fifth borne */

.fifth.borne .station {
  background-color: #0B1C61;
  color: #FFFFFF;
  font-family: 'Roboto', sans-serif;
  font-weight: 600;
  font-size: 4em;
  text-align: center;
  outline: none;
  border: none;
  max-width: 75%;
  padding: 5px 5%;
  margin: 5% auto;
}

.fifth.borne img {
  border-radius: 12px;
  border: #EA4C89 4px solid;
}

/* All elements without exceptions */
.hidden {
  display: none;
}